body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: #323041;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
  min-height: 100vh;
}

p {
  margin: 0;
}

h2 {
  margin: 0;
  font-weight: 300;
}

h3 {
  margin: 0;
}

li {
  max-width: 675px;
  width: 100%;
}

.App-header {
  background-color: #26232d;
  min-height: 12vh;
  color: white;
  text-align: left;
  margin-bottom: 50px;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  margin-left: 30px;
  margin-right: 30px;
}

.left-header {
  margin-top: 32px;
  margin-right: 15px;
  margin-bottom: 0;
  text-transform: uppercase;
  letter-spacing: 0.2ch;
  font-size: 1.3em;
  font-weight: 300;
}

.right-header {
  margin-top: 40px;
}

.subtitle {
  margin-left: 30px;
  margin-top: 5px;
  margin-right: 20px;
  padding-bottom: 15px;
  color: #aaa;
}

.btn {
  display: inline-block;
  padding: 8px 16px;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.2ch;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  transition: 0.2s;
  font-family: 'Oswald';
}

.btn.focus,
.btn:focus,
.btn:hover {
  color: #ffffff;
  text-decoration: none;
}

.btn-primary {
  color: #fff;
  background-color: #6e6a8f;
}

.btn-primary:hover {
  color: #fff;
  background-color: #585472;
}

.posts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
}

.post {
  height: 240px;
  overflow: hidden;
  font-size: 1.3em;
  /* border: 1px solid red; */
  margin-bottom: 35px;
  background-color: #403E4F;
  box-shadow: 9px 12px 9px 0px rgba(41, 39, 53, 0.75);
}

.post-title {
  letter-spacing: 0.25ch;
  text-transform: uppercase;
  font-weight: 300;
}

.post-inside {
  padding: 28px;
  padding-top: 0px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.post-link {
  text-align: left;
  color: #8A9AF0;
  cursor: pointer;
  font-size: 1.1rem;
}

.post-date {
  font-weight: 300;
  font-family: 'PT Serif', serif;
  display: block;
  padding-left: 10px;
  letter-spacing: 0.25rem;
  font-size: 0.75rem;
  line-height: 2;
  text-transform: uppercase;
  background: linear-gradient( to right, #4a4c5a, #403E4D );
}

.excerpt {
  font-size: 1rem;
  font-family: 'PT Serif', serif;
}



.fullpost {
  margin: 0 auto;
  width: 100%;
  max-width: 675px;
  text-align: left;
  font-size: 1.35em;
  margin-bottom: 35px;
  background-color: #403E4F;
  box-shadow: 9px 12px 9px 0px rgba(41, 39, 53, 0.75);
}

.fullpost-title {
  margin-bottom: 7px;
  letter-spacing: 0.25ch;
  text-transform: uppercase;
  font-weight: 400;
}

.fullpost-inside {
  padding: 28px;
  padding-top: 20px;
}

.fullpost-date {
  font-weight: 300;
  font-family: 'PT Serif', serif;
  display: block;
  padding-left: 10px;
  margin-top: 15px;
  letter-spacing: 0.25rem;
  font-size: 0.75rem;
  line-height: 2;
  text-transform: uppercase;
  background: linear-gradient( to right, #4a4c5a, #403E4D );
}

.fullpost-content {
  margin-top: 15px;
  padding-bottom: 20px;
  line-height: 1.7em; 
  font-size: 1.05rem;
  font-family: 'PT Serif', serif;
}

.backButton {
  text-align: left;
  text-decoration: none;
  padding-top: 22px;
  color: #8A9AF0;
  cursor: pointer;
  font-size: 1.1rem;
}

.newPost {
  margin: 0 auto;
  width: 100%;
  max-width: 650px;
  text-align: left;
  font-size: 1.35em;
  margin-bottom: 35px;
  background-color: #403E4F;
  box-shadow: 9px 12px 9px 0px rgba(41, 39, 53, 0.75);
}

.newPost h2 {
  margin: 30px;
}

.newPost-form {
  margin: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.newPost-title {
  width: 100%;
}

.newPost-title input {
  width: 100%;
  height: 25px;
  border-radius: 4px;
}

.newPost-content {
  margin-top: 20px;
  width: 100%;
}

.newPost-content-text {
  width: 100%;
  height: 120px;
  border-radius: 4px;
}

.newPost-button {
  margin-bottom: 20px;
  margin-top: 20px;
}
@media only screen and (max-width: 500px) {
    .header {
        margin-left: 18px;
        margin-right: 18px;
    }

    .subtitle {
        margin-left: 18px;
    }

    .fullpost-inside {
        padding: 18px;
    }

    .fullpost-content {
        font-size: 1rem;
        line-height: 1.6rem;
    }

    .left-header {
        font-size: 1rem;
    }

    .fullpost {
        margin-bottom: 0px;
    }

    .post {
        margin-bottom: 20px;
    }
}

@media only screen and (max-width: 450px) {
    .App-header {
        margin-bottom: 0px;
    }
    .left-header {
        margin-top: 15px;
    }
    .right-header {
        margin-top: 15px;
    }
    .posts {
        margin-top: 20px;
    }
    .fullpost {
        margin-top: 20px;
    }
}

@media only screen and (max-width: 400px) {
    .App-header {
        margin-bottom: 0px;
    }

    .subtitle {
        font-size: 0.8rem;
    }
    .fullpost {
        margin-top: 0px;
    }
}
